import * as React from "react"
import { StaticImage } from "gatsby-plugin-image"

import Layout from "../components/layout"
import Seo from "../components/seo"
import * as styles from "../components/index.module.css"

const IndexPage = () => (
  <Layout>
    <div class="intro"></div>
    <div class="cards">
      <div class="card">
        <span class="number">100</span>
        <span class="label">Parties uniques</span>
      </div>
      <div class="card">
        <span class="number">4</span>
        <span class="label">Modes de jeu</span>
      </div>
      <div class="card">
        <div class="top">
          <span class="number">3</span>
          <StaticImage
            src="../images/alien.png"
            loading="eager"
            width={120}
            quality={95}
            formats={["auto", "webp", "avif"]}
            alt=""
            style={{ marginBottom: `var(--space-3)` }}
          />
        </div>
        <span class="label">Invasions d'aliens</span>
      </div>
    </div>
    <div class="content">
      <p class="background">
        Crimes mystérieux, vols sophistiqués, complots dangereux…
        <br/>La station spatiale est en danger, et la police est dépassée. Seul un détective comme vous peut résoudre les différentes enquêtes !
        <br/>
        <br/>
        Êtes-vous le détective que nous attendons tous ? 
      </p>
      <h2 class="background">100 parties disponibles !</h2>
    </div>
  </Layout>
)

export const Head = () => <Seo title="Home" />

export default IndexPage
